import React, { Component } from 'react';

export default class ListarConcedentes extends Component {
    constructor(){
        super()
        this.state = {
            listaDeConcedentes : []
        }

        this.carregarListaDeConcedentes = this.carregarListaDeConcedentes.bind(this)
    }

    componentWillMount(){
        this.carregarListaDeConcedentes()
    }

    carregarListaDeConcedentes(){
        const {api} = this.props
        api.Concedentes.ListaConcedentes(
            (listaDeConcedentes) => this.setState({listaDeConcedentes}),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarAcordoDeCooperacao(id){
        const {api} = this.props
        return `${api.Concedentes.getBaseUrl()}/${id}/baixar`
    }

    baixar(link){
        const {api} = this.props
        const token = api.Auth.accessToken()

        fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.blob())
        .then(blob => {
                // 2. Create blob link to download
                const url = window.URL.createObjectURL(new Blob([blob]));
                // window.open(url, '_blank') // open in a local popup
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', `${url.substring(url.lastIndexOf('/')+1)}.pdf`);
              
                // 3. Append to html page
                document.body.appendChild(link);
              
                // 4. Force download
                link.click();
              
                // 5. Clean up and remove the link
                link.parentNode.removeChild(link);
        })
    }

    render() {
        const {listaDeConcedentes} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Concedentes</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>CNPJ</th>
                            <th>Nome</th>
                            <th>Ramo de Atividade</th>
                            <th>Telefone</th>
                            <th>Email</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeConcedentes.map(concedente => {
                            const downloadLink = this.linkParaBaixarAcordoDeCooperacao(concedente.id)

                            return (
                                <tr key={concedente.id}>
                                    <td>{concedente.cnpj}</td>
                                    <td>{concedente.nome}</td>
                                    <td>{concedente.ramo_atividade}</td>
                                    <td>{concedente.telefone}</td>
                                    <td>{concedente.email}</td>
                                    <td>
                                        {/* <button onClick={() => window.open(downloadLink, "_blank")}>Baixar</button> */}
                                        <button onClick={() => this.baixar(downloadLink)}>Baixar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
