import React, { Component } from 'react';

export default class ListarConcedentesPropostos extends Component {
    constructor(){
        super()
        this.state = {
            listaDePropostas : []
        }

        this.carregarListaDePropostas = this.carregarListaDePropostas.bind(this)
        this.efetivarProposta = this.efetivarProposta.bind(this)
        this.apagarProposta = this.apagarProposta.bind(this)
    }

    componentWillMount(){
        this.carregarListaDePropostas()
    }

    carregarListaDePropostas(){
        const {api} = this.props
        api.Concedentes.ListaPropostas(
            (listaDePropostas) => this.setState({listaDePropostas}),
            (erro) => console.log('erro',erro)
        )
    }

    efetivarProposta(proposta){
        const {api} = this.props

        const concedente = {
            ...proposta,
            ativo : true
        }

        api.Concedentes.AtualizaConcedente(concedente.id, concedente, 
            window.location.href = " #",
            (erro) => console.log('erro',erro)
        )
    }

    apagarProposta(id){
        const {api} = this.props

        api.Concedentes.RemovePropostaPelaId(id,
            this.setState(prevState => {
                const listaDePropostas = prevState.listaDePropostas.filter(proposta => proposta.id !== id)
                return {listaDePropostas}
            }),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarAcordoDeCooperacao(id){
        const {api} = this.props
        return `${api.Concedentes.getBaseUrl()}/${id}/baixar`
    }

    baixar(link){
        const {api} = this.props
        const token = api.Auth.accessToken()

        fetch(link, {
            headers : {
                "Authorization" : `Bearer ${token}`
            }
        })
        .then(res => res.blob())
        .then(blob => {
                // 2. Create blob link to download
                const url = window.URL.createObjectURL(new Blob([blob]));
                // window.open(url, '_blank') // open in a local popup
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', `${url.substring(url.lastIndexOf('/')+1)}.pdf`);
              
                // 3. Append to html page
                document.body.appendChild(link);
              
                // 4. Force download
                link.click();
              
                // 5. Clean up and remove the link
                link.parentNode.removeChild(link);
        })
    }

    render() {
        const {listaDePropostas} = this.state

        return (
            <div>
                <h5>Rela√ß√£o de Propostas para Concedente</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>CNPJ</th>
                            <th>Nome</th>
                            <th>Ramo de Atividade</th>
                            <th>Telefone</th>
                            <th>Email</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDePropostas.map(proposta => {
                            const downloadLink = this.linkParaBaixarAcordoDeCooperacao(proposta.id)

                            return (
                                <tr key={proposta.id}>
                                    <td>{proposta.cnpj}</td>
                                    <td>{proposta.nome}</td>
                                    <td>{proposta.ramo_atividade}</td>
                                    <td>{proposta.telefone}</td>
                                    <td>{proposta.email}</td>
                                    <td>
                                        <button onClick={() => this.baixar(downloadLink)}>Baixar Acordo</button>
                                        <button onClick={() => this.efetivarProposta(proposta)}>Efetivar</button>
                                        <button onClick={() => this.apagarProposta(proposta.id)}>Apagar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
